.container{max-width: 1920px;padding: 0 15px;margin: 0 auto;}
.content-container{max-width: 1580px;margin: 0 auto;padding: 0 30px;}



h1.index-banner__title, .sectiont__title{
    font-weight: 600; 
    font-size: 1rem; 
    line-height: 1.36; 
    
    @media (min-width: 576px){
        font-size: 1.2rem;
    }

    @media (min-width: 728px){
        font-size: 1.46rem;
    }
    @media (min-width: 900px){
        font-size: 1.619rem;
    }
    @media (min-width: 1300px){
        font-size: 2.619rem;
    }
    @media (min-width: 1570px){
        font-size: 3.619rem;
    }

}



h2.sectiont-content__title{
    font-weight: 500; 
    font-size: 1.5rem; 
    line-height: 1.36;
    margin-bottom: 30px;

@media (min-width: 576px){font-size: 1.8rem;}
@media (min-width: 728px){font-size: 2.42rem;}
@media (min-width: 992px){font-size: 2.6rem; margin: 0;}
@media (min-width: 1300px){ font-size: 2.8rem;}
@media (min-width: 1570px){  font-size: 3.42rem;}
}

.breadcrumbs{
    
    display: none;

    @media (min-width: 728px){
        display: block;
        padding: 20px 0;
    }

    @media (min-width: 992px){
        padding: 40px 0;
    }


    &__link{
        font-weight: 600;
        font-size: 0.667rem;
        line-height: 1.36;
        letter-spacing: 0.028rem;
        text-transform: uppercase;   
    }

    & a{
        color: #000000;
    
        @media (min-width: 992px){
            transition: opacity 0.8s ease 0s;
            opacity: 1;
    
            &:hover{opacity: 0.5;}
        }
    }

    & span{
        color: #B2B2B2;
    
    }

}


.icon-readcrumbs-separation{
    font-weight: 600;
    font-size: 0.667rem;
    line-height: 1.36;
    letter-spacing: 0.028rem;
    text-transform: uppercase;
    color: #B2B2B2;
    margin: 0 10px;
}


.sectiont__title{
    margin-bottom: 40px;
}

.paragraph_separator{
    margin: 20px 0;
    border-bottom: 2px solid #E5E5E5;
}




